<template>
  <div>
    <el-row class="warp">
      <el-row :gutter="20" class="main-title">
        <!--标题-->
        <el-col :span="6">
          <i class="title-mark"></i>
          <h2 class="title-txt">文章编辑</h2>
        </el-col>
        <!--功能键-->
        <el-col :span="18"></el-col>
      </el-row>
      <!-- Form 组件提供了表单验证的功能，只需要通过 rule 属性传入约定的验证规则，并 Form-Item 的 prop 属性设置为需校验的字段名即可。具体可以参考官网：http://element.eleme.io/#/zh-CN/component/form -->
      <el-col :span="24" class="warp-main">
        <el-form ref="infoForm" :model="infoForm" :rules="rules" label-width="120px">
          <el-form-item label="标题" prop="title">
            <el-input v-model="infoForm.title"></el-input>
          </el-form-item>
          <el-form-item label="副标题" prop="s_title">
            <el-input v-model="infoForm.s_title"></el-input>
          </el-form-item>
          <el-form-item label="时间" prop="time">
            <el-input v-model="infoForm.time"></el-input>
          </el-form-item>
          <el-form-item label="来源" prop="source">
            <el-input v-model="infoForm.source"></el-input>
          </el-form-item>
          <el-form-item label="开头" prop="start">
            <el-input v-model="infoForm.start"></el-input>
          </el-form-item>
          <el-form-item label="结尾" prop="end">
            <el-input v-model="infoForm.end"></el-input>
          </el-form-item>
          <div v-for="(item,index) in content" v-if="content">
                <el-form-item label="图片"  v-if="item.src">
                  <div class="flex">
                    <img :src="item.src" class="rendersrc"/>
                    <el-button class="del" @click="delContent(index)">删除</el-button>
                  </div>
                </el-form-item>
                <el-form-item label="段落" v-if="item.value !== undefined">
                  <div class="flex">
                    <el-input v-model="item.value"></el-input>
                    <el-button class="del" @click="delContent(index)">删除</el-button>
                  </div>
                </el-form-item>
          </div>
          <div class="footer">
            <el-button type="primary" @click="onAddTXT">添加段落</el-button>
            <div class="flex img">
              <el-button type="primary" @click="onAddIMG">添加图片
                <quill-editor
                    ref="newEditor"
                    :options="editorOption"
                    @change="onEditorChange($event)"
                  >
                </quill-editor>
              </el-button>
            </div>
          </div>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">最终确认提交</el-button>
          </el-form-item>
        </el-form>
      </el-col>


    </el-row>
  </div>
</template>

<script>
  import { quillEditor } from 'vue-quill-editor' //调用编辑器
    import {quillRedefine} from 'vue-quill-editor-upload'

  // 时间获取函数
  Date.prototype.Format = function (fmt) {
    var o = {
        "M+": this.getMonth() + 1, // 月份
        "d+": this.getDate(), // 日
        "h+": this.getHours(), // 小时
        "m+": this.getMinutes(), // 分
        "s+": this.getSeconds(), // 秒
        "q+": Math.floor((this.getMonth() + 3) / 3), // 季度
        "S": this.getMilliseconds() // 毫秒
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt;
  }

  export default {
    data() {
      return {
        infoForm: {
          title: '',  // 标题
          s_title: '',  // 副标题
          time: new Date().Format("yyyy-MM-dd"),
          source: '', // 内容来源
          start: '', // 开头
          end: '', // 结尾
        },
        content: [
          {
            value: '',
          },
        ], // 内容
        editorOption:{
          modules:{
              toolbar:[
                    ['image']
              ]
          }
        },
        //表单验证
        rules: {
          title: [
            {required: true, message: '请输入标题', trigger: 'blur'}
          ],
          // content: [
          //   {required: true, message: '请输入详细内容', trigger: 'blur'}
          // ]
        },
      }
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    mounted() {
      //初始化
    },
    created () {
      // this.editorOption = quillRedefine(
      //   {
      //     // 图片上传的设置
      //     uploadConfig: {
      //       action:  '/aaa',  // 必填参数 图片上传地址
      //       // 必选参数  res是一个函数，函数接收的response为上传成功时服务器返回的数据
      //       // 你必须把返回的数据中所包含的图片地址 return 回去
      //       res: (respnse) => {
      //         return respnse.info  // 这里切记要return回你的图片地址
      //       }
      //     },
      //     modules:{
      //         toolbar:[
      //               ['image']
      //         ]
      //     }
      //   }
      // )
    },
    methods: {
      onEditorReady(editor) {
      },
      // 修改
      delContent (index) {
        this.content.splice(index, 1)
      },
      onAddTXT () {
        this.content.push({
          value: ''
        })
      },
      onAddIMG () {

      },
      onEditorChange (e) {
        const { html } = e
        //匹配图片（g表示匹配所有结果i表示区分大小写）
        var imgReg = /<img.*?(?:>|\/>)/gi;
        //匹配src属性
        var srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
        var arr = html.match(imgReg);
        for (var i = 0; i < arr.length; i++) {
          var src = arr[i].match(srcReg);
          //获取图片地址
          if(src[1]){
            this.content.push({
              src: src[1]
            })
          }
        }
      },
      onSubmit() {
        //this.$refs.infoForm.validate，这是表单验证
        this.$refs.infoForm.validate((valid) => {
          if(valid) {
            const parmas = {
              ...this.infoForm,
              content: this.content,
            }
            console.log('parmas :', parmas)
            this.$axios.post('/add', parmas)
              .then(res => {
                if(res.errCode == 200) {
                  alert('上传成功', JSON.stringify(parmas))
                }
              })
              .catch(e => alert(JSON.stringify(parmas)))
          }
        });
      },
    },
    components: {
      //使用编辑器
      quillEditor,
      quillRedefine
    }
  }
</script>

<style>
  .ql-toolbar {
      display: flex;
      width: 45px;
  }
  .flex {
    display: flex;
  }
  .del {
    margin-left: 10px;
  }
  .ql-container {
    display: none;
  }
  .footer {
    display: flex;
    margin: 0 auto;
    margin-bottom: 40px;
    margin-left: 120px;
  }
  .footer button {
    margin-right: 10px
  }
  .img {
    margin-left: 40px;
  }
  .rendersrc {
    max-height: 100px;
  }
</style>



